*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --ff-main: "Roboto", sans-serif;
  --footer-bg: #e5e5e5;
  --light: #fff;
  --dark: #000;
  --accent: #b92f2c;
  --accent-hover: #982725;
  --light-hover: #d7d4d4;
  --pagination-nex-prev: #f7f7f7;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  font-size: inherit;
  margin: 0;
}

p {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

html, body {
  height: 100%;
}

body {
  height: 100%;
  font-family: var(--ff-main);
  background-color: var(--main-bg);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16667;
  overflow-y: scroll;
}

body.no-scroll {
  height: 100vh;
  width: 100vw;
  padding-right: 6px;
  overflow-y: hidden;
}

.wrapper {
  min-height: 100%;
  flex-direction: column;
  display: flex;
}

main {
  flex: auto;
}

.section {
  padding: 30px 0 50px;
}

.section.popular {
  margin-bottom: 70px;
}

@media screen and (min-width: 768px) {
  .search__root, .popular__root, .library-root {
    grid-column-gap: 15px;
    grid-row-gap: 30px;
    grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
    display: grid;
  }
}

.trending__title, .popular__title, .search__title {
  margin-bottom: 20px;
}

.hidden {
  display: none;
}

.container {
  max-width: 320px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 1280px;
  }
}

::-webkit-scrollbar {
  height: 100%;
  width: 6px;
  position: fixed;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
