.backdrop {
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  cursor: pointer;
  background-color: rgba(0, 0, 0, .7);
  transition: opacity .2s linear, visibility .2s linear;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
}

.backdrop.visible {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

.modal {
  max-width: 320px;
  min-height: 440px;
  background-color: var(--light);
  opacity: 0;
  visibility: hidden;
  border: 1px solid #d3d3d3;
  border-radius: 4px;
  margin: auto;
  padding: 40px 20px 20px;
  transition: opacity .25s linear, visibility .25s linear, transform .3s linear;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transform: scale(.9);
}

.modal.visible {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
}

@media screen and (max-width: 767.9px) {
  .modal {
    width: calc(100% - 40px);
    height: calc(100% - 20px);
    overflow: auto;
  }
}

@media screen and (min-width: 768px) {
  .modal {
    max-width: 705px;
    max-height: 480px;
  }
}

@media screen and (min-width: 1280px) {
  .modal {
    max-height: 520px;
    max-width: 805px;
  }
}

.modal__close-btn {
  z-index: 2;
  width: 35px;
  height: 35px;
  color: var(--dark);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  transition: color .25s linear, transform .25s linear;
  position: absolute;
  top: 4px;
  right: 0;
